import Link from "next/link";
import { useRouter } from "next/router";
import { ReactNode, useState, useEffect, useMemo } from "react";
import { useSession, useSupabaseClient } from "@supabase/auth-helpers-react";
import type { CSSProperties } from "react";

type DynamicHeaderProps = {
  rightActions?: ReactNode;
  variant?: "default" | "landing";
  scrollContainerRef?: React.RefObject<HTMLDivElement>;
};

const baseNavLink: CSSProperties = {
  fontFamily: "'Inter', -apple-system, BlinkMacSystemFont, sans-serif",
  fontWeight: 600,
  fontSize: 13,
  letterSpacing: "0.02em",
  display: "inline-flex",
  alignItems: "center",
  gap: 8,
  padding: "10px 16px",
  borderRadius: 999,
  transition: "background-color 0.2s ease, color 0.2s ease",
  textDecoration: "none",
};

export default function DynamicHeader({ rightActions, variant = "default", scrollContainerRef }: DynamicHeaderProps) {
  const [isVisible, setIsVisible] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);
  const [isInitialized, setIsInitialized] = useState(false);
  const router = useRouter();
  const session = useSession();
  const supabase = useSupabaseClient();

  const theme = useMemo(() => {
    if (variant === "landing") {
      return {
        height: 120,
        wrapper: {
          transition: "height 0.35s ease, opacity 0.35s ease",
        },
        container: {
          height: 120,
          display: "flex",
          alignItems: "center",
          background: "linear-gradient(180deg, rgba(2, 6, 23, 0.95), rgba(2, 6, 23, 0.9))",
          backdropFilter: "saturate(140%) blur(8px)",
          WebkitBackdropFilter: "saturate(140%) blur(8px)",
          borderBottom: "2px solid rgba(255,255,255,0.15)",
          borderTop: "1px solid rgba(255,255,255,0.1)",
          width: "100%",
        },
        inner: {
          width: "100%",
          maxWidth: 2000,
          margin: "0 auto",
          padding: "0 24px 0 0",
          display: "flex",
          alignItems: "center",
          justifyContent: "flex-start",
          gap: 20,
        },
        nav: {
          display: "flex",
          alignItems: "center",
          gap: 28,
          marginLeft: "auto",
        },
        navLink: {
          ...baseNavLink,
          padding: "0",
          color: "#e2e8f0",
        },
        primary: {
          ...baseNavLink,
          padding: "12px 22px",
          background: "linear-gradient(135deg, #3b82f6, #1d4ed8)",
          color: "#fff",
          border: "1px solid rgba(255,255,255,0.18)",
        },
        subtle: {
          ...baseNavLink,
          padding: "12px 22px",
          background: "rgba(255,255,255,0.08)",
          border: "1px solid rgba(255,255,255,0.14)",
          color: "#e2e8f0",
        },
        logo: {
          height: 120,
        },
        tagline: null,
      } as const;
    }

    return {
      height: 72,
      wrapper: {
        transition: "height 0.28s ease, opacity 0.28s ease",
      },
      container: {
        height: 72,
        display: "flex",
        alignItems: "center",
        backgroundColor: "rgba(252, 253, 255, 0.9)",
        borderBottom: "1px solid rgba(15, 23, 42, 0.08)",
        backdropFilter: "blur(16px)",
        WebkitBackdropFilter: "blur(16px)",
        boxShadow: "0 12px 28px rgba(15, 23, 42, 0.06)",
        width: "100%",
      },
      inner: {
        width: "100%",
        maxWidth: 1200,
        margin: "0 auto",
        padding: "0 24px",
        display: "flex",
        alignItems: "center",
        gap: 20,
      },
      nav: {
        marginLeft: "auto",
        display: "flex",
        alignItems: "center",
        gap: 12,
      },
      navLink: {
        ...baseNavLink,
        color: "#1f2937",
      },
      primary: {
        ...baseNavLink,
        color: "#fff",
        background: "linear-gradient(135deg, #1d4ed8, #2563eb)",
        border: "1px solid rgba(37, 99, 235, 0.4)",
        boxShadow: "0 10px 26px rgba(37, 99, 235, 0.22)",
      },
      subtle: {
        ...baseNavLink,
        color: "#1d4ed8",
        backgroundColor: "rgba(37, 99, 235, 0.12)",
        border: "1px solid rgba(37, 99, 235, 0.2)",
      },
      logo: {
        height: 40,
      },
      tagline: {
        fontSize: 12,
        color: "#475569",
        letterSpacing: "0.08em",
        textTransform: "uppercase",
      },
    } as const;
  }, [variant]);

  useEffect(() => {
    const timer = window.setTimeout(() => setIsInitialized(true), 600);
    return () => window.clearTimeout(timer);
  }, []);

  useEffect(() => {
    const scrollElement = scrollContainerRef?.current || window;

    const handleScroll = () => {
      if (!isInitialized) return;

      const currentScrollY =
        scrollElement === window
          ? window.scrollY
          : (scrollElement as HTMLDivElement).scrollTop ?? 0;

      const newVisibility = currentScrollY < lastScrollY || currentScrollY <= 12;
      if (newVisibility !== isVisible) {
        setIsVisible(newVisibility);
      }

      setLastScrollY(currentScrollY);
    };

    scrollElement.addEventListener("scroll", handleScroll, { passive: true });
    return () => {
      scrollElement.removeEventListener("scroll", handleScroll);
    };
  }, [isInitialized, scrollContainerRef, isVisible, lastScrollY]);

  const handleLogout = async () => {
    await supabase.auth.signOut();
    router.push("/login").catch(() => {});
  };

  const wrapperStyle: CSSProperties = {
    height: isVisible ? theme.height : 0,
    overflow: "hidden",
    opacity: isVisible ? 1 : 0,
    position: "relative",
    zIndex: 30,
    ...theme.wrapper,
  };

  return (
    <div style={wrapperStyle}>
      <header style={theme.container as CSSProperties}>
        <div style={theme.inner as CSSProperties}>
          <Link
            href="/"
            style={{
              display: "flex",
              alignItems: "center",
              gap: 12,
              textDecoration: "none",
            }}
          >
            <img
              src="/Logo.png"
              alt="Finance CV"
              style={{
                height: theme.logo.height,
                width: "auto",
                objectFit: "contain",
                filter: variant === "landing" ? "drop-shadow(0 10px 30px rgba(0,0,0,0.45))" : "none",
              }}
            />
            {theme.tagline && (
              <div style={{ display: "flex", flexDirection: "column" }}>
                <span
                  style={{
                    fontSize: 16,
                    fontWeight: 700,
                    color: "#0f172a",
                    letterSpacing: "-0.01em",
                    fontFamily: "'Inter', -apple-system, BlinkMacSystemFont, sans-serif",
                  }}
                >
                  Finance CV
                </span>
                <span
                  style={{
                    fontFamily: "'Inter', -apple-system, BlinkMacSystemFont, sans-serif",
                    ...theme.tagline,
                  }}
                >
                  Atelier de CV premium
                </span>
              </div>
            )}
          </Link>

          <div style={theme.nav as CSSProperties}>
            {rightActions ?? (
              <>
                <Link href="/cv" style={theme.navLink as CSSProperties}>
                  Générateur
                </Link>
                <Link href="/mes-cv" style={theme.navLink as CSSProperties}>
                  Mes CV
                </Link>
                <Link href="/a-propos" style={theme.navLink as CSSProperties}>
                  À propos
                </Link>
                <Link href="/cv" style={theme.primary as CSSProperties}>
                  Créer un CV
                </Link>
                {session?.user ? (
                  <button
                    type="button"
                    onClick={handleLogout}
                    style={{
                      ...theme.subtle,
                      cursor: "pointer",
                      border: "none",
                      background: variant === "landing" ? "rgba(15,15,40,0.35)" : theme.subtle.backgroundColor,
                      color: variant === "landing" ? "#e2e8f0" : (theme.subtle as CSSProperties).color,
                    }}
                  >
                    Se déconnecter
                  </button>
                ) : (
                  <Link href="/login" style={theme.subtle as CSSProperties}>
                    Se connecter
                  </Link>
                )}
              </>
            )}
          </div>
        </div>
      </header>
    </div>
  );
}
